.ButtonModal {
  width: 20%;
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
}

.WideButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 90%;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  padding: 0 8%;
}
body main {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}
body main form {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-column: 1/-1;
  justify-items: center;
}
body main form input {
  width: 100%;
  box-sizing: border-box;
}
body main form label {
  justify-self: start;
  margin-bottom: 1px;
}
body main form .WideButton {
  width: 100%;
}